body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-pingfang-sc: "PingFang SC";
  --font-zihunjikehei: zihunjikehei;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;

  /* Colors */
  --color-white: #fff;
  --color: #035199;
  --color-darkslateblue-100: rgba(3, 81, 153, 0.8);
  --color-mediumblue: #4f14cc;
  --color-mediumseagreen: rgba(43, 158, 89, 0.8);
  --color-darkslategray-100: #3d3d3d;
  --color-darkslategray-200: #333;
  --color-darkgray: #999;
  --color-gray-100: rgba(255, 255, 255, 0.4);
}
